import React from 'react';

let useProxy = false;
let isProduction = true;
let isLandShown = true;

export class DataModel {
  static instance = null;
  data = {}

  constructor() {
    console.log(useProxy ? 'enabled' : 'disabled');
  }

  isProduction() {
    return isProduction;
  }

  isLandShown() {
    return isLandShown;
  }

  init() {}

  static shared() {
    if (DataModel.instance == null) {
      DataModel.instance = new DataModel();
    }

    return this.instance;
  }

  store(code, json) {
    console.log(code, 'is stored');
    this.data[code] = json;
  }

  load(code) {
    var result = this.data[code];
    if (result != null) {
      console.log(code, 'is loaded');
    }
    return result;
  }
}

class DataSource extends React.Component {
  proxyUrl = 'https://cors-anywhere.herokuapp.com/';

  constructor() {
    super();

    DataModel.shared().init();

    this.state = {
      data: [],
      period: '1y'
    };
  }

  componentWillUnmount() {

  }

  getProxy() {
    if (useProxy) {
      return this.proxyUrl;
    }

    return "";
  }

  fetchData(codes) {
    for (var index in codes) {
      var code = codes[index];

      if (code === 'chartGetHousePrice') {
        this.fetchLandPrice();
        continue;
      }

      this.fetchYahooFinance(code);
    }
  }

  async fetchLandPrice() {
    try {
      const response = await fetch(`https://api.jonghyunho.com/v1/chartGetHousePrice`);
      const json = await response.json();
      this.setState({ data: json });
    } catch (error) {
      console.log(error);
    }
  }

  fetchYahooFinance(code) {
    var json = DataModel.shared().load(code);
    if (json != null) {
      console.log('cache hit', code);
      this.setState({ data: json });
      return;
    }

    console.log('fetching', code);
    code = encodeURI(code);

    var begin_date = (Date.parse('2000-01-01') / 1000).toString();
    var end_date = Date.now().toString();

    var targetUrl = 'https://query1.finance.yahoo.com/v8/finance/chart/' + code + '?period1=' + begin_date + '&period2=' + end_date + '&interval=1d';

    var x = new XMLHttpRequest();
    x.open('GET', this.getProxy() + targetUrl);

    var self = this;
    x.onload = x.onerror = function() {
      if (x.status === 404) return;
      code = decodeURI(code);
      var result = JSON.parse('{ "'+ code +'" : ' + x.responseText + '}');
      result[code] = result[code].chart.result[0];

      DataModel.shared().store(code, result[code]);
      self.setState({ data: result })
    };
    x.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
    x.send('');
  }

  formatDate(timestamp) {
    var d = new Date(timestamp * 1000),
    month = '' + (d.getMonth() + 1),
    day = '' + d.getDate(),
    year = d.getFullYear();

    if (month.length < 2) month = '0' + month;
    if (day.length < 2) day = '0' + day;

    return [year, month, day].join('-');
  }

  getPrevDate(period) {
    if (period === '3m') {
      return new Date(new Date() - 86400 * 1000 * 30 * 3) / 1000;
    } else if (period === '6m') {
      return new Date(new Date() - 86400 * 1000 * 30 * 6) / 1000;
    } else if (period === '1y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12) / 1000;
    } else if (period === '3y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 3) / 1000;
    } else if (period === '5y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 5) / 1000;
    } else if (period === '10y') {
      return new Date(new Date() - 86400 * 1000 * 30 * 12 * 10) / 1000;
    }
    return Date.parse('2000-01-01') / 1000;
  }
}

export default DataSource;