import './App.css';

import ContentCard from './ContentCard';

import * as d3 from 'd3';

class ComparisonCard extends ContentCard {
  codes = ['^KS11','KRW=X'];
  json = {}
  value = {}

  constructor() {
    super();

    this.state = {
      period: '1y',
      data_1: [],
      data_2: []
    };
  }

  async componentDidMount() {
    this.fetchData(this.codes);
    return;
  }

  getValues() {
    let codes = this.codes;

    var indices = [];
    var values_1 = [];
    var values_2 = [];

    var response = this.state.data;

    for (var key in response) {
      this.json[key] = response[key];
    }

    for (var index in codes) {
      var code = codes[index];
      var result = this.json[code];
      if (result === undefined) {
        return [[], [], []];
      }
    }

    var biggerMinDate = 0;

    for (index in codes) {
      code = codes[index];
      result = this.json[code];
      var adjclose = result.indicators.adjclose[0].adjclose;

      if (result.timestamp.length > 0) {
        if (biggerMinDate < result.timestamp[0]) {
          biggerMinDate = result.timestamp[0];
        }
      }

      for (var ts_index in result.timestamp) {
        var date = result.timestamp[ts_index];
        if (index == 0) {
          this.value[date] = [parseFloat(adjclose[ts_index]).toFixed(2) * 1, undefined];
        } else {
          var v = this.value[date];
          if (v != undefined) v = v[0];
          this.value[date] = [v, parseFloat(adjclose[ts_index]).toFixed(2) * 1];
        }
      }
    }

    let prevDate = this.getPrevDate(this.state.period);

    for (date in this.value) {
      if (date < prevDate) continue;
      if (date < biggerMinDate) continue;
      indices.push(this.formatDate(date));

      var values = this.value[date];
      values_1.push(values[0]);
      values_2.push(values[1]);
    }

    values_1 = this.normalize(values_1);
    values_2 = this.normalize(values_2);

    return [indices, values_1, values_2];
  }

  normalize(arr, is_std = true) {
    var mean = d3.mean(arr);
    var std = d3.deviation(arr);

    for (var index in arr) {
      var value = arr[index];
      arr[index] = (value - mean) / (is_std ? std : 1.0);
    }

    return arr;
  }

  getData() {
    var values = this.getValues();
    if (values[0].length === 0) {
      return null;
    }

    const data = {
      labels: values[0],
      datasets: [
        {
          label: '코스피',
          data: values[1],
          fill: false,
          backgroundColor: '#2279B5',
          borderColor: '#2279B5',
          borderWidth: 2,
          pointRadius: 0,
          hitRadius: 2,
          cubicInterpolationMode: 'monotone',
          lineTension: 0.1,
          spanGaps: true
        },
        {
          label: '환율',
          data: values[2],
          fill: false,
          backgroundColor: '#ff7245',
          borderColor: '#ff7245',
          borderWidth: 2,
          pointRadius: 0,
          hitRadius: 2,
          cubicInterpolationMode: 'monotone',
          lineTension: 0.1,
          spanGaps: true
        }
      ]
    };

    return data
  }
}

export default ComparisonCard;